<template>
  <div>
    <div style>
      <el-button-group>
        <el-button @click="freshen" type="primary" size="small" class="margin-right10">刷新当前页</el-button>
        <el-button @click="add()" type="primary" size="small" icon="el-icon-plus">新增</el-button>
        <el-button @click="modify()" type="primary" size="small" icon="el-icon-finished">编辑</el-button>
        <Poptip confirm title="配置删除需谨慎，请确认!" @on-ok="remove">
          <el-button type="danger" size="small" icon="el-icon-minus">删除</el-button>
        </Poptip>
      </el-button-group>
      <el-input placeholder="请输入内容" style="width:200px;float:right;" size="small" v-model="input">
        <el-button slot="append" icon="el-icon-search" @click="search()"></el-button>
      </el-input>
    </div>
    <div style="display:none;">
      <Modal
        v-model="modalAdd"
        draggable
        title="Txle"
        @on-ok="save()"
        @on-cancel="cancel"
        footer-hide
      >
        <el-form :model="model" :rules="rules" ref="ruleForm" label-width="100px">
          <el-form-item label="服务名称" prop="servicename">
            <el-autocomplete
              popper-class="my-autocomplete"
              v-model="model.servicename"
              :fetch-suggestions="querySearchByServicename"
              placeholder="请选择服务名称"
              @select="handleSelectByServicename"
            >
              <template slot-scope="{ item }">
                <div class="name">{{ item.value }}</div>
              </template>
            </el-autocomplete>
          </el-form-item>
          <el-form-item label="服务代号" prop="instanceid">
            <el-autocomplete
              popper-class="my-autocomplete"
              v-model="model.instanceid"
              :fetch-suggestions="querySearchByInstanceid"
              placeholder="请选择服务代号"
              @select="handleSelectByInstanceid"
            >
              <template slot-scope="{ item }">
                <div class="name">{{ item.value }}</div>
              </template>
            </el-autocomplete>
          </el-form-item>
          <el-form-item label="服务类别" prop="category">
            <el-autocomplete
              popper-class="my-autocomplete"
              v-model="model.category"
              :fetch-suggestions="querySearchByCategory"
              placeholder="请选择服务类别"
            >
              <template slot-scope="{ item }">
                <div class="name">{{ item.value }}</div>
              </template>
            </el-autocomplete>
          </el-form-item>
          <el-form-item label="类型" prop="type">
            <Select v-model="model.type" clearable placeholder="请选择类型">
              <Option
                v-for="item in type_options"
                :value="item.value"
                :key="item.value"
              >{{ item.label }}</Option>
            </Select>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <Select v-model="model.status" clearable placeholder="请选择状态">
              <Option
                v-for="item in status_options"
                :value="item.value"
                :key="item.value"
              >{{ item.label }}</Option>
            </Select>
          </el-form-item>
          <el-form-item label="能力" prop="ability">
            <Select v-model="model.ability" clearable placeholder="请选择能力">
              <Option
                v-for="item in ability_options"
                :value="item.value"
                :key="item.value"
              >{{ item.label }}</Option>
            </Select>
          </el-form-item>
          <el-form-item label="值" prop="value">
            <el-autocomplete
              v-model="model.value"
              popper-class="my-autocomplete"
              :fetch-suggestions="querySearchByValue"
              placeholder="请选择值"
            >
              <template slot-scope="{ item }">
                <span class="addr">{{ item.label }}</span>
              </template>
            </el-autocomplete>
          </el-form-item>
          <el-form-item label="备注" prop="remark">
            <Input v-model="model.remark" placeholder="请输入备注" />
          </el-form-item>
          <el-form-item>
            <Poptip confirm :title="modalSubmit" @on-ok="save">
              <el-button type="primary">提交</el-button>
            </Poptip>
            <el-button @click="cancel()" class="reset-btn">重置</el-button>
          </el-form-item>
        </el-form>
      </Modal>
    </div>
    <el-table
      tooltip-effect="dark"
      empty-text="暂无数据"
      ref="multipleTable"
      @selection-change="selectionChange"
      @sort-change="sortChange"
      :data="tableData"
      style="width: 100%; margin-top:10px; margin-bottom: 20px;"
      row-key="id"
      border
      stripe
      :default-sort="{prop: orderName, order: direction}"
    >
      <el-table-column type="selection" width="60" align="center"></el-table-column>
      <el-table-column
        v-for="(item, index) in tableLabel"
        :key="index"
        :prop="item.prop"
        :label="item.label"
        v-if="item.show"
        sortable="custom"
        show-overflow-tooltip
        filter-placement="left"
      >
        <template slot-scope="scope">
          <span
            v-if="scope.column.property === 'status'&&
            (scope.row[scope.column.property]==='正常'||
             scope.row[scope.column.property]==='历史'||
             scope.row[scope.column.property]==='废弃')"
          >
            <i
              class="el-icon-circle-check"
              style="font-size:22px;color:#981AF2;"
              title="正常"
              v-if="scope.row.status=='正常'"
            ></i>
            <i
              class="el-icon-time"
              style="font-size:22px;color:#8A148A;"
              title="历史"
              v-if="scope.row.status=='历史'"
            ></i>
            <i
              class="el-icon-delete"
              style="font-size:22px;color:#C830C8;"
              title="废弃"
              v-if="scope.row.status=='废弃'"
            ></i>
          </span>
          <span v-else>{{scope.row[scope.column.property]}}</span>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align:right;">
      <Page
        v-if="total>pageSize"
        :total="total"
        :current="pageNo"
        @on-change="changePage"
        :page-size="pageSize"
        show-total
        show-elevator
      ></Page>
    </div>
  </div>
</template>
<script>
import {
	getTable,
	getTableData,
	postConfig,
	deleteConfig,
	findGlobalTxServerNames,
	findGlobalTxServerInstanceIds,
	findGlobalTxServerCategories,
	findDataDictionaryByKey,
} from '@/api/configs'
export default {
	data() {
		return {
			servernames: [],
			serverinstanceids: [],
			servercategorys: [],
			tableLabel: [],
			tableData: [],
			pageSize: 10,
			pageNo: 1,
			total: 0,
			input: '',
			orderName: 'updatetime',
			direction: 'descending',
			modalSubmit: '配置修改需谨慎，请确认!',
			modalAdd: false,
			model: {
				servicename: '',
				instanceid: '',
				category: '',
				type: '',
				status: '',
				ability: '',
				value: '',
				remark: '',
				id: -1,
			},
			type_options: [],
			status_options: [],
			ability_options: [],
			value_options: [],
			selection: [],
			rules: {},
		}
	},
	mounted() {
		this.init()
		this.loadAllServicenames()
		this.loadAllOptions()
		this.loadAllValues()
	},
	methods: {
		selectionChange(rows) {
			this.selection = rows
		},
		remove() {
			if (this.selection.length === 0) {
				return this.$Message.info('请勾选要删除的内容')
			}
			let array = this.selection.map(m => {
				return m.id
			})
			deleteConfig(array.join(',')).then(res => {
				if (res.recordSet) {
					this.$Message.success('删除操作成功')
					this.pageNo = 1
					this.bindData()
				} else {
					this.$Message.error('删除操作失败')
				}
			})
		},
		querySearchByValue(queryString, cb) {
			let options = this.value_options
			let results = queryString ? options.filter(this.createFilterByValue(queryString)) : options
			cb(results)
		},
		createFilterByValue(queryString) {
			return option => {
				return option.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
			}
		},
		querySearchByCategory(queryString, cb) {
			let servercategorys = this.servercategorys
			let results = queryString
				? servercategorys.filter(this.createFilterByCategory(queryString))
				: servercategorys
			cb(results)
		},
		createFilterByCategory(queryString) {
			return servercategory => {
				return servercategory.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
			}
		},
		querySearchByInstanceid(queryString, cb) {
			let serverinstanceids = this.serverinstanceids
			let results = queryString
				? serverinstanceids.filter(this.createFilterByInstanceid(queryString))
				: serverinstanceids
			cb(results)
		},
		createFilterByInstanceid(queryString) {
			return serverinstanceid => {
				return serverinstanceid.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
			}
		},
		querySearchByServicename(queryString, cb) {
			var servernames = this.servernames
			var results = queryString
				? servernames.filter(this.createFilterByServicename(queryString))
				: servernames
			cb(results)
		},
		createFilterByServicename(queryString) {
			return servername => {
				return servername.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
			}
		},
		handleSelectByServicename(item) {
			let servicename = encodeURI(this.model.servicename)
			findGlobalTxServerInstanceIds(servicename).then(res => {
				this.serverinstanceids = res.recordSet.map(e => {
					return { value: e }
				})
			})
		},
		handleSelectByInstanceid(item) {
			let servicename = encodeURI(this.model.servicename)
			let instanceid = encodeURI(this.model.instanceid)
			findGlobalTxServerCategories(servicename, instanceid).then(res => {
				this.servercategorys = res.recordSet.map(e => {
					return { value: e }
				})
			})
		},
		loadAllServicenames() {
			findGlobalTxServerNames().then(res => {
				this.servernames = res.recordSet.map(e => {
					return { value: e }
				})
			})
		},
		loadAllValues() {
			findDataDictionaryByKey('config-center-value').then(res => {
				for (let key in res.recordSet) {
					let value = res.recordSet[key]
					let label = key
					this.value_options.push({ value, label })
				}
			})
		},
		loadAllOptions() {
			findDataDictionaryByKey('config-center-type').then(res => {
				for (let key in res.recordSet) {
					let value = res.recordSet[key]
					let label = key
					this.type_options.push({ value, label })
				}
			})
			findDataDictionaryByKey('config-center-status').then(res => {
				for (let key in res.recordSet) {
					let value = res.recordSet[key]
					let label = key
					this.status_options.push({ value, label })
				}
			})
			findDataDictionaryByKey('config-center-ability').then(res => {
				for (let key in res.recordSet) {
					let value = res.recordSet[key]
					let label = key
					this.ability_options.push({ value, label })
				}
			})
		},
		save() {
			this.$refs['ruleForm'].validate(valid => {
				if (valid) {
					postConfig(this.model).then(res => {
						if (res.recordSet) {
							this.pageNo = 1
							this.bindData()
							this.$refs.multipleTable.clearSelection()
							this.$Message.success('保存数据成功')
							this.modalAdd = false
						} else {
							this.$Message.error('保存数据失败')
						}
					})
				}
			})
		},
		modify() {
			this.modalSubmit = '配置修改需谨慎，请确认!'
			if (this.selection.length === 0) {
				return this.$Message.info('请勾选要编辑的内容')
			} else if (this.selection.length > 1) {
				return this.$Message.info('不能批量编辑内容')
			}
			this.modalAdd = true
			let _model = this.selection[0]
			if (_model) {
				this.model['servicename'] = _model['servicename']
				this.model['instanceid'] = _model['instanceid']
				this.model['category'] = _model['category']
				this.model['type'] = _model['type_db'] + ''
				this.model['status'] = _model['status_db'] + ''
				this.model['ability'] = _model['ability_db'] + ''
				this.model['value'] = _model['value']
				this.model['remark'] = _model['remark']
				this.model['id'] = _model['id']
			}
		},
		add() {
			this.modalSubmit = '配置添加需谨慎，请确认!'
			this.modalAdd = true
			this.model = {
				servicename: '',
				instanceid: '',
				category: '',
				type: '',
				status: '',
				ability: '',
				value: '',
				remark: '',
				id: -1,
			}
		},
		cancel() {
			this.$refs['ruleForm'].resetFields()
		},
		sortChange(scope) {
			this.orderName = scope.prop
			this.direction = scope.order
			this.bindData()
		},
		changePage(e) {
			this.pageNo = e
			this.bindData()
		},
		search() {
			if (!this.input) {
				return this.$Message.info('请输入内容')
			}
			this.pageNo = 1
			this.bindData()
		},
		bindData() {
			let params = [this.pageNo, this.pageSize, this.orderName, this.direction, this.input]
			this.getData(...params)
		},
		getData(pageNo, pageSize, orderName, direction, keyword) {
			getTableData(...arguments).then(res => {
				this.tableData.splice(0, this.pageSize)
				this.tableData = res.recordSet
				this.total = parseInt(res.recordsTotal)
			})
		},
		init() {
			let params = [this.pageNo, this.pageSize, this.orderName, this.direction, this.input]
			getTable(...params).then(res => {
				res[0].recordSet.forEach(item => {
					this.tableLabel.push({
						prop: item.field,
						label: item.fieldname,
						show: item.display === 'true',
					})
					this.rules[item.field] = []
					if (item.notnull) {
						let please = item.type === 'varchar' ? '请填写' : '请选择'
						this.rules[item.field].push({
							required: true,
							message: please + item.fieldname,
							trigger: 'blur',
						})
					}
					if (item.fieldlength > 0 && item.type !== 'int') {
						this.rules[item.field].push({
							max: item.fieldlength,
							message: '长度不大于' + item.fieldlength + '个字符',
							trigger: 'blur',
						})
					}
				})
				res[1].recordSet.forEach(column => {
					var item = {}
					this.tableLabel.forEach(label => {
						item[label.prop] = column[label.prop]
					})
					this.tableData.push(item)
					this.total = parseInt(res[1].recordsTotal)
				})
			})
		},
		freshen() {
			this.input = ''
			this.bindData()
		},
	},
}
</script>
<style lang="less" >
.el-autocomplete {
	width: 100%;
	.el-input__inner {
		height: 30px;
		line-height: 30px;
		font-size: 12px;
	}
}
.reset-btn {
	margin-left: 20px;
}
.my-autocomplete {
	li {
		line-height: normal;
		padding: 7px;
		.name {
			text-overflow: ellipsis;
			overflow: hidden;
		}
		.highlighted .addr {
			color: #ddd;
		}
	}
}
</style>
